<template>
  <div style="display: flex">
    <div class="intro-text" style="min-height:calc(100vh - 60px);width: 155px;background-color: #ecedf1;flex-shrink: 0;">
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/standard" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">操作标准</span>
        </el-menu-item>
        <el-menu-item index="/recordTask"   >
          <i class="el-icon-caret-right"></i>
          <span slot="title">任务记录</span>
        </el-menu-item>
        <el-menu-item index="/maintain" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">维护记录</span>
        </el-menu-item>
        <el-menu-item index="/problem" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">问题反馈</span>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 120px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>
    <div style="padding: 20px 20px 20px 20px;width: 100%">
      <div style="padding-bottom: 20px">
        <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容" v-model="searchText"></el-input>
        <el-button type="primary" @click="load(1)">查询</el-button>
        <el-button type="info" @click="reset">重置</el-button>
      </div>
      <el-table :data="RecordTaskList" stripe :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" ></el-table-column>
        <el-table-column label="任务名称"  prop="taskName" align="center" ></el-table-column>
        <el-table-column label="任务类型"  prop="type" align="center" ></el-table-column>
        <el-table-column label="领取人员"  prop="userName" align="center"></el-table-column>
        <el-table-column label="开始时间"  prop="timeBegin" align="center"></el-table-column>
        <el-table-column label="完成时间"  prop="timeEnd" align="center"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
                size="mini"
                @click="toTask(scope.row)">查看</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      RecordTaskList:[],
      pageNum: 1,   // 当前的页码
      pageSize: 10,  // 每页显示的个数
      total: 0,
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      searchText: '',
    }
  },
  created() {
    this.load()
  },
  methods: {
    load(pageNum) {
      if(pageNum) this.pageNum = pageNum
      this.$request.get("/recordTask/getPage", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          searchText: this.searchText
        }
      }).then(res=>{
        this.RecordTaskList = res.data.records
        this.total = res.data.total
        console.log(this.RecordTaskList)
      })
    },
    reset(){
      this.searchText = ''
    },
    handleSelectionChange(rows) {   // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
    toTask(row){
      switch (row.type) {
        case '临时任务':
          this.$router.push({path: '/taskTemp', query: {taskId: row.taskId}})
          break;
        case '维修任务':
          break;
        case '设备点检任务':
          this.$router.push({ path: '/checkRecord', query: {taskId:row.taskId}});
          break;
        case '仪表点检任务':
          this.$router.push({path: '/taskCheckMeter', query: {taskId: row.taskId}})
          break;
      }
    }
  }
}
</script>

<style scoped>

</style>